import React, { useState } from 'react';
import { Card, Button, Typography, Spin, message } from 'antd';
import { LoginOutlined } from '@ant-design/icons';
import { generateRandomString, generateCodeVerifier, generateCodeChallenge } from '../utils/pkceUtils';

const { Title, Paragraph, Text } = Typography;

/**
 * Admin UIOAuth2
 * OAuth2
 */
const AdminAuthInitiator: React.FC = () => {
  const [loading, setLoading] = useState<boolean>(false);

  // OAuth2
  const initiateAuth = async () => {
    try {
        debugger
      setLoading(true);
      
      // PKCE
      const codeVerifier = generateCodeVerifier();
      const codeChallenge = await generateCodeChallenge(codeVerifier);
      
      // state
      const state = generateRandomString(16);
      
      // code_verifiersessionStorage
      sessionStorage.setItem('pkce_code_verifier', codeVerifier);
      sessionStorage.setItem('pkce_state', state);
      
      console.log('PKCE :');
      console.log('- Code Verifier ():', codeVerifier.substring(0, 10) + '...');
      console.log('- Code Challenge ():', codeChallenge.substring(0, 10) + '...');
      
      // URL
      const params = new URLSearchParams({
        response_type: 'code',
        client_id: 'webapp-client',
        redirect_uri: `${window.location.origin}/test/oauth2-callback`,
        scope: 'read write openid profile offline_access',
        state: state,
        code_challenge: codeChallenge,
        code_challenge_method: 'S256',
        access_type: 'offline'  // refresh_token
      });
      
      // 
      const authUrl = `/oauth2/authorize?${params.toString()}`;
      console.log(':', authUrl);
      
      window.location.href = authUrl;
    } catch (error) {
      console.error(':', error);
      message.error('');
      setLoading(false);
    }
  };
  
  return (
    <div style={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      minHeight: '100vh',
      padding: '20px'
    }}>
      <Card 
        style={{ 
          width: '100%', 
          maxWidth: 500,
          boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
        }}
        title={
          <Title level={3} style={{ marginBottom: 0, textAlign: 'center' }}>
            Admin UI
          </Title>
        }
      >
        <Spin spinning={loading} tip="...">
          <Paragraph style={{ marginBottom: 24 }}>
            Admin UIOAuth2PKCE
            
          </Paragraph>
          
          <ul style={{ marginBottom: 24 }}>
            <li>PKCEcode_verifiercode_challenge</li>
            <li>SSO/oauth2/authorize</li>
            <li></li>
            <li></li>
            <li>token</li>
          </ul>
          
          <div style={{ textAlign: 'center', marginTop: 24 }}>
            <Button 
              type="primary" 
              icon={<LoginOutlined />}
              size="large"
              onClick={initiateAuth}
              disabled={loading}
              style={{ height: '48px', minWidth: '200px' }}
            >
              
            </Button>
          </div>
          
          <div style={{ marginTop: 24, padding: 16, background: '#f9f9f9', borderRadius: 4 }}>
            <Text type="secondary" style={{ fontSize: '13px' }}>
              <strong></strong>
              <br />
              client_id: webapp-client
              <br />
              redirect_uri: {window.location.origin}/test/oauth2-callback
              <br />
              scope: read write openid profile offline_access
            </Text>
          </div>
        </Spin>
      </Card>
    </div>
  );
};

export default AdminAuthInitiator; 