import axios, { AxiosResponse, AxiosRequestConfig } from 'axios';

// OAuth2 Token
export interface TokenRequestParams {
  grant_type: string;
  code?: string;
  code_verifier?: string;
  client_id: string;
  redirect_uri: string;
  refresh_token?: string;
}

// OAuth2 Token
export interface TokenResponse {
  access_token: string;
  token_type: string;
  expires_in: number;
  refresh_token?: string;
  scope?: string;
  id_token?: string;
}

// 
export interface LoginRequestParams {
  username: string;
  password: string;
}

// OAuth2 
export interface OAuth2Error {
  error: string;
  error_description?: string;
  error_uri?: string;
}

// axios
const apiClient = axios.create({
  baseURL: '', // baseURL
  timeout: 10000,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded',
  }
});

// 
apiClient.interceptors.request.use((config) => {
  if (config.method === 'post' && config.data && config.headers['Content-Type'] === 'application/x-www-form-urlencoded') {
    // URL
    config.data = new URLSearchParams(config.data).toString();
  }
  return config;
});

// 
apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.data) {
      return Promise.reject({
        ...error,
        oauth2Error: error.response.data as OAuth2Error
      });
    }
    return Promise.reject(error);
  }
);

// token
export const requestToken = async (
  params: TokenRequestParams, 
  tokenEndpoint: string = '/oauth2/token'
): Promise<TokenResponse> => {
  try {
    const response: AxiosResponse<TokenResponse> = await apiClient.post(
      tokenEndpoint,
      params
    );
    return response.data;
  } catch (error: any) {
    console.error('Token:', error);
    throw error.oauth2Error || error;
  }
};

// token
export const exchangeCodeForToken = async (
  code: string,
  codeVerifier: string,
  clientId: string,
  redirectUri: string,
  tokenEndpoint: string = '/oauth2/token'
): Promise<TokenResponse> => {
  return requestToken({
    grant_type: 'authorization_code',
    code,
    code_verifier: codeVerifier,
    client_id: clientId,
    redirect_uri: redirectUri
  }, tokenEndpoint);
};

// token
export const refreshToken = async (
  refreshToken: string,
  clientId: string,
  tokenEndpoint: string = '/oauth2/token'
): Promise<TokenResponse> => {
  return requestToken({
    grant_type: 'refresh_token',
    refresh_token: refreshToken,
    client_id: clientId,
    redirect_uri: '' // redirect_uri
  }, tokenEndpoint);
};

// 
export const loginWithCredentials = async (
  params: LoginRequestParams,
  loginEndpoint: string = '/login'
): Promise<AxiosResponse> => {
  try {
    // 0
    const response = await apiClient.post(
      loginEndpoint,
      params,
      {
        maxRedirects: 0,
        validateStatus: (status) => {
          return status >= 200 && status < 400;
        }
      }
    );
    return response;
  } catch (error: any) {
    console.error(':', error);
    throw error;
  }
};

export default apiClient; 