import React, { useEffect, useState } from 'react';
import { Card, Result, Button, Typography, Alert, Spin, Divider } from 'antd';
import { CheckCircleOutlined, ExclamationCircleOutlined, HomeOutlined, ReloadOutlined } from '@ant-design/icons';
import { exchangeCodeForToken } from '../utils/apiClient';
// tokenService
import { saveTokens } from '../services/tokenService';

const { Title, Paragraph, Text } = Typography;

/**
 * OAuth2
 * Token
 */
const OAuth2Callback: React.FC = () => {
  // 
  const [loading, setLoading] = useState<boolean>(true);
  const [success, setSuccess] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [tokenData, setTokenData] = useState<any>(null);
  const [code, setCode] = useState<string | null>(null);
  
  useEffect(() => {
    // 
    const handleCallback = async () => {
      try {
        // URLstate
        const urlParams = new URLSearchParams(window.location.search);
        const authCode = urlParams.get('code');
        const state = urlParams.get('state');
        const errorParam = urlParams.get('error');
        const errorDescription = urlParams.get('error_description');
        
        // 
        if (errorParam) {
          throw new Error(`: ${errorParam}${errorDescription ? ` - ${errorDescription}` : ''}`);
        }
        
        // 
        if (!authCode) {
          throw new Error('');
        }
        
        setCode(authCode);
        console.log(':', authCode);
        
        // sessionStoragecode_verifier
        const codeVerifier = sessionStorage.getItem('pkce_code_verifier');
        if (!codeVerifier) {
          throw new Error('PKCE');
        }
        
        // URIURL
        const redirectUri = `${window.location.origin}/test/oauth2-callback`;
        
        console.log('Token:', {
          tokenEndpoint: '/oauth2/token',
          grantType: 'authorization_code',
          redirectUri: redirectUri,
          codeLength: authCode.length,
          codeVerifierLength: codeVerifier.length
        });
        
        // exchangeCodeForTokenToken
        const responseData = await exchangeCodeForToken(
          authCode,
          codeVerifier,
          'webapp-client',
          redirectUri
        );
        
        // 
        console.log('Token:', responseData);
        setTokenData(responseData);
        
        // TokenlocalStorage
        saveTokens(responseData);
        
        // PKCE
        sessionStorage.removeItem('pkce_code_verifier');
        sessionStorage.removeItem('pkce_state');
        
        setSuccess(true);
        setLoading(false);
      } catch (error: any) {
        console.error('Token:', error);
        setError(error.message || '');
        setLoading(false);
      }
    };
    
    handleCallback();
  }, []);
  
  // 
  const goToInitiator = () => {
    window.location.href = '/test/admin-auth-initiator';
  };
  
  // 
  const goToHome = () => {
    window.location.href = '/';
  };
  
  if (loading) {
    return (
      <div style={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        minHeight: '100vh',
        padding: '20px'
      }}>
        <Card style={{ width: '100%', maxWidth: 500 }}>
          <div style={{ textAlign: 'center', padding: '30px 0' }}>
            <Spin size="large" />
            <Paragraph style={{ marginTop: 20 }}>
              Token...
            </Paragraph>
            {code && (
              <Alert
                message=""
                description={`: ${code.substring(0, 10)}...`}
                type="success"
                showIcon
                style={{ marginTop: 20, textAlign: 'left' }}
              />
            )}
          </div>
        </Card>
      </div>
    );
  }
  
  return (
    <div style={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      minHeight: '100vh',
      padding: '20px'
    }}>
      <Card 
        style={{ width: '100%', maxWidth: 600 }}
        title={
          <Title level={3} style={{ marginBottom: 0, textAlign: 'center' }}>
            OAuth2
          </Title>
        }
      >
        {success ? (
          <>
            <Result
              status="success"
              icon={<CheckCircleOutlined />}
              title=""
              subTitle="OAuth2Token"
              extra={[
                <Button 
                  type="primary" 
                  key="home" 
                  icon={<HomeOutlined />}
                  onClick={goToHome}
                >
                  
                </Button>,
                <Button 
                  key="again" 
                  icon={<ReloadOutlined />}
                  onClick={goToInitiator}
                >
                  
                </Button>
              ]}
            />
            
            {tokenData && (
              <>
                <Divider>Token</Divider>
                <div style={{ background: '#f5f5f5', padding: 16, borderRadius: 4, maxHeight: 300, overflow: 'auto' }}>
                  <pre style={{ margin: 0 }}>
                    {JSON.stringify(tokenData, null, 2)}
                  </pre>
                </div>
                
                <Alert
                  message="Token"
                  description="TokenlocalStorageAPI"
                  type="info"
                  showIcon
                  style={{ marginTop: 20 }}
                />
              </>
            )}
          </>
        ) : (
          <Result
            status="error"
            icon={<ExclamationCircleOutlined />}
            title=""
            subTitle={error || ''}
            extra={[
              <Button 
                type="primary" 
                key="retry"
                onClick={goToInitiator}
              >
                
              </Button>,
              <Button key="home" onClick={goToHome}></Button>
            ]}
          />
        )}
      </Card>
    </div>
  );
};

export default OAuth2Callback; 